import React from "react";
import menu from "../img/menu.svg";
import back_white from "../img/back_white.svg";
import back from "../img/back.svg";
import { Link } from "react-router-dom";

const Nav = props => {
  return (
    <div className="nav dropdown expand">
      {props.dest === "menu" ? (
        <div>
          <img className="menu" src={menu} alt="" />
          <div className="dropdown-content">
            <button onClick={props.logout}>LOGOUT</button>
          </div>
        </div>
      ) : (
        <Link to={props.dest}>
          <img
            className="menu"
            src={props.color === "white" ? back_white : back}
            alt=""
          />
        </Link>
      )}
      {!props.edit ? null : (
        <Link to={props.edit}>
          <span className="item-name light-font">edit</span>
        </Link>
      )}
    </div>
  );
};

export default Nav;
